import {HTTP} from '@/common/http-common'

export default {
  state: {
    scriptureId: 1,
    lang: null,
    verses: null,
    verseIndex: null,
    languages: null,
    chapters: null,
    deferredPrompt: null,
    verseOfDayId: null,
    verseOfDayImage: null
  },

  getters: {
    scriptureId (state) {
      if (state.scriptureId == null) {
        state.scriptureId = localStorage.getItem('scriptureId')
      }
      return state.scriptureId
    },

    lang (state) {
      if (state.lang == null) {
        state.lang = localStorage.getItem('lang')
      }
      return state.lang
    },

    languages (state) {
      if (state.languages == null) {
        state.languages = JSON.parse(localStorage.getItem('languages'))
      }
      return state.languages
    },

    chapters (state) {
      if (state.chapters == null) {
        state.chapters = JSON.parse(localStorage.getItem('chapters'))
      }
      return state.chapters
    },

    verses (state) {
      if (state.verses == null) {
        state.verses = JSON.parse(localStorage.getItem('verses'))
      }
      return state.verses
    },

    verseIndex (state) {
      if (state.verseIndex == null) {
        state.verseIndex = localStorage.getItem('verseIndex')
        if (state.verseIndex == null) {
          state.verseIndex = 0
        }
      }
      return state.verseIndex
    },

    verse (state, getters) {
      if (getters.verses) {
        return getters.verses[getters.verseIndex]
      } else {
        return null
      }
    },

    verseText (state, getters) {
      if (getters.verse) {
        return getters.verse.content[getters.lang].title.replace(/\n/g, '<br />')
      }
    },

    sanskritVerseText (state, getters) {
      if (getters.verse) {
        return getters.verse.content.sa.title.replace(/\n/g, '<br />')
      }
    }
  },

  mutations: {
    verses (state, payload) {
      state.verses = payload
      localStorage.setItem('verses', JSON.stringify(state.verses))
    },

    lang (state, payload) {
      state.lang = payload
      localStorage.setItem('lang', state.lang)
    },

    languages (state, payload) {
      state.languages = payload
      localStorage.setItem('languages', JSON.stringify(state.languages))
    },

    chapters (state, payload) {
      state.chapters = payload
      localStorage.setItem('chapters', JSON.stringify(state.chapters))
    },

    incrementVerseIndex (state) {
      if (state.verseIndex !== 700) {
        state.verseIndex = parseInt(state.verseIndex) + 1
        localStorage.setItem('verseIndex', state.verseIndex)
      }
    },

    decrementVerseIndex (state) {
      if (state.verseIndex !== 0) {
        state.verseIndex = parseInt(state.verseIndex) - 1
        localStorage.setItem('verseIndex', state.verseIndex)
      }
    },

    verseIndex (state, payload) {
      state.verseIndex = payload
      localStorage.setItem('verseIndex', state.verseIndex)
    },

    resetState (state) {
      state.scriptureId = 1
      state.lang = null
      state.verses = null
      state.verseIndex = null
      state.isLoading = false
      state.error = null

      // clearing local storage
      localStorage.clear()

      // clearing cache
      caches.keys().then(function (names) {
        for (let name of names) {
          caches.delete(name)
        }
      })
    }
  },

  actions: {
    fetchVerses ({commit, getters}, payload) {
      commit('isLoading', true)
      commit('lang', payload.lang)
      HTTP.get('verses', {params: {scriptureId: getters.scriptureId, lang: getters.lang}}).then(response => {
        commit('verses', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    fetchLanguages ({commit, getters}, payload) {
      commit('isLoading', true)
      HTTP.get('languages').then(response => {
        commit('languages', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    fetchChapters ({commit, getters}, payload) {
      console.log('in fechchapters pre')
      commit('isLoading', true)
      HTTP.get('chapters', {params: { scriptureId: getters.scriptureId, lang: getters.lang }}).then(response => {
        commit('chapters', response.data)
        console.log('in fechchapters post', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    incrementVerseIndex ({commit}) {
      commit('incrementVerseIndex')
    },
    decrementVerseIndex ({commit}) {
      commit('decrementVerseIndex')
    },

    changeChapter ({commit, getters}, payload) {
      console.log('in change chapter action')
      var length = getters.verses.length
      for (var i = 0; i < length; i++) {
        console.log()
        var v = getters.verses[i]
        if (v.chapterId === payload.chapterId) {
          commit('verseIndex', i)
          break
        }
      }
    },

    resetState ({commit}) {
      commit('resetState')
    }
  }
}



