import {HTTP} from '@/common/http-common'

export default {
  state: {
    profile: null,
    token: null,
    helloToken: null,
    helloProfile: null
  },

  getters: {
    user (state) {
      return state.user
    },

    isLoggedIn (state, getters) {
      return getters.token != null
    },

    token (state) {
      if (state.token == null) {
        state.token = localStorage.getItem('token')
      }
      return state.token
    },

    helloToken (state) {
      if (state.helloToken == null) {
        state.helloToken = JSON.parse(localStorage.getItem('hello')).google.access_token
      }
      return state.helloToken
    },

    helloProfile (state) {
      if (state.helloProfile == null) {
        state.helloProfile = JSON.parse(localStorage.getItem('helloProfile'))
      }
      return state.helloProfile
    }
  },

  mutations: {
    user (state, payload) {
      state.user = payload
      localStorage.setItem('user', JSON.stringify(state.user))
    },

    token (state, payload) {
      state.token = payload
      localStorage.setItem('token', JSON.stringify(state.token))
    },

    helloProfile (state, payload) {
      state.helloProfile = payload
      localStorage.setItem('helloProfile', JSON.stringify(state.helloProfile))
    }
  },

  actions: {
    getToken ({commit, getters}) {
      var p = getters.helloProfile
      var t = getters.helloToken
      var reqObj = {
        id: p.id,
        displayName: p.displayName,
        name: p.name,
        image: p.image,
        email: p.email,
        gender: p.gender,
        url: p.url,
        thirdPartyAccessToken: t
      }

      console.log('in gettoken')
      commit('isLoading', true)
      HTTP.get('token', {params: reqObj}).then(response => {
        commit('token', response.data)
        commit('isLoading', false)
      }).catch(e => {
        commit('error', e)
        commit('isLoading', false)
      })
    },

    helloProfile ({commit}, payload) {
      commit('helloProfile', payload)
    },

    logout ({commit}) {
      commit('token', null)
      localStorage.removeItem('token')
      commit('helloToken', null)
      localStorage.removeItem('hello')
      commit('helloProfile', null)
      localStorage.removeItem('helloProfile')
    }
  }
}



