<template>
  <v-container class="mycontainer">
    <v-layout row wrap v-if="isLoading">
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
            <br><br>
            <v-progress-circular indeterminate :size="100" color="amber"></v-progress-circular>
        </v-flex>
    </v-layout>
    <v-layout row wrap v-if="!isLoading">
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
          <v-slide-x-transition>
            <v-card v-show="show">
                <v-card-media src="">
                </v-card-media>
                <v-card-title>
                    <div>
                        <span class="primaryText" v-html="verseText"></span> <br><br>
                        <span class="secondaryText" v-html="sanskritVerseText"></span>
                    </div>
                </v-card-title>
            </v-card>
          </v-slide-x-transition>
        </v-flex>

        <v-flex xs12 sm4 offset-sm4>
        <br><br>
        <v-btn color="gray" flat @click="gotoVerse">Continue Reading</v-btn>
        <br/><br/><br/><br/><br/><br/>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      show: true
    }
  },

  computed: {
    verseText () {
      return this.$store.getters.verseText
    },
    sanskritVerseText () {
      return this.$store.getters.sanskritVerseText
    },
    isLoading () {
      return this.$store.getters.isLoading
    }
  },

  methods: {
    gotoVerse () {
      this.$router.push({
        path: 'Verse'
      })
    }
  }
}
</script>
<style>
    .fixedButton {
        position: fixed;
        bottom: 30%;
        right: 30px;
    }
    .primaryText {
        font-family: 'Arya', sans-serif;
        font-size: medium;
        color: #212121;
    }
    .secondaryText {
        font-family: 'Karma', serif;
        font-size: medium;
        color: #757575;
    }

@media only screen and (max-width: 599px) {
    .mycontainer {
        padding: 5px !important;
    }
}
</style>



